.home-wrapper {
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 1000px;
  z-index: -1;
  position: relative;
  text-align: center;
  color: white;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.67);
}

.centered-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
